import React from "react";
import { Box, Typography } from "@mui/material";
import { useForm } from "react-hook-form";
import useAxios from "../../hooks/useAxios";
import "./Panel.css";
import { useState } from "react";

export default function TrackingOrder() {
  const[url,setUrl] = useState("")

  const { response } = useAxios({
    method: "get",
    url
  });
  const SubmitHandeler = (data) => {
    setUrl(`/users?password=${data.orderNumber}`)
   
  };
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  return (
    <Box>
      <Typography
        component="h2"
        sx={{ fontWeight: "bold", marginBottom: "1.5rem" }}
      >
                .
      </Typography>
      <form onSubmit={handleSubmit(SubmitHandeler)}>
        <label className="lable-panel"> </label>
        <input
          {...register("orderNumber", {
            required: "      ",
            message: "      ",
          })}
          type="text"
          className="input-panel"
          placeholder="         "
        />
        <p className="alert">{errors.orderNumber?.message}</p>
        <input type="submit" className="submit-btn-panel" value="" />
      </form>
    </Box>
  );
}
